.stars {
  display: flex;

  &__star {
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
    width: 16px;
    height: 16px;
    margin-right: 4px;
  }

  &__star:first-child {
    margin-left: 0;
  }

  &--1 :nth-child(1),
  &--2 :nth-child(-n + 2),
  &--3 :nth-child(-n + 3),
  &--4 :nth-child(-n + 4),
  &--5 :nth-child(n) {
    background-image: url("../images/star-active.svg");
  }
}


